.List {
    margin-top: 30px;

    font-weight: 500;
    font-size: 18px;
}

.Item {
    display: flex;
    justify-content: space-between;
}

.Item:not(:last-child) {
    margin-bottom: 10px;
}

.Text {
    font-weight: 500;
    font-size: 18px;
}

.Span {
    color: rgba(0, 0, 0, 0.6);
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
}

.Button {
    width: 80px;
    height: 30px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    color: rgb(112, 112, 112);
    outline: none;
}
.Button:hover {
    border-bottom: 2px solid rgb(255, 0, 0);
    color: rgb(255, 0, 0);
    transform: scale(1.1);
}
