.Form {
    margin-top: 10px;
    margin-bottom: 20px;
}

.Label {
    display: block;
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 20px;
}

.Input {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    padding: 10px;
    width: 95%;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    cursor: pointer;
    font-size: 15px;
}

.Input:focus {
    outline-color: rgba(179, 32, 32, 0.486);
    background-color: #fff;
}

.Button {
    margin-bottom: 15px;
    padding: 10px 20px;
    background-color: rgba(82, 82, 82, 0.466);
    box-shadow: 2px 2px 4px rgb(0 0 0 / 30%), 0px 2px 2px rgb(0 0 0 / 8%),
        1px 4px 6px rgb(0 0 0 / 30%);
    border-radius: 4px;
    border: none;
    outline: none;
    opacity: 0.8;
    transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 300ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    color: rgb(226, 226, 226);
    font-weight: 700;
    font-size: 18px;
}

.Button:hover {
    color: tomato;
    background-color: white;
    opacity: 1;
}
